import React, { useEffect, useRef, useState } from 'react';
import './StopWatch.scss';

type Props = {
  isFinished:boolean;
  setTimeRes: React.Dispatch<React.SetStateAction<string>>;
  setIsStarted:React.Dispatch<React.SetStateAction<boolean>>;
};

export const StopWatch:React.FC<Props> = ({
  isFinished,
  setTimeRes,
  setIsStarted,
}) => {
  const [timer, setTimer] = useState(0);
  const [isRunning, setIsRunning] = useState(false);

  const timeInterval = useRef(null);

  const handleStart = () => {
    if (isRunning) {
      return;
    }

    setIsRunning(true);
    setIsStarted(true);
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    timeInterval.current = setInterval(() => {
      setTimer((prev) => prev + 1);
    }, 10);
  };

  const formatTime = (time: number) => {
    const minutes = Math.floor(time / 6000)
      .toString()
      .padStart(2, '0');
    const seconds = Math.floor((time / 100) % 60)
      .toString()
      .padStart(2, '0');
    const milliseconds = (time % 100).toString().padStart(3, '0');

    return { minutes, seconds, milliseconds };
  };

  const { minutes, seconds, milliseconds } = formatTime(timer);
  const handlePause = () => {
    if (!isRunning) {
      return;
    }

    setIsRunning(false);
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    clearInterval(timeInterval.current);
    setTimeRes(`${minutes} хвилин та ${seconds} секунд`);
  };

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const handleReset = () => {
    setIsRunning(false);
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    clearInterval(timeInterval.current);
    setTimer(0);
  };

  useEffect(() => {
    if (isFinished === true) {
      handlePause();
    }
  }, [isFinished]);

  return (
    <div className="stopWatch">
      <h2>Щоб почати натисніть Start</h2>
      <div className="timer-container">
        <div className="timer-box">
          <span>{minutes}</span>
        </div>
        <span className="colon">:</span>
        <div className="timer-box">
          <span>{seconds}</span>
        </div>
        <span className="colon">:</span>
        <div className="timer-box">
          <span>{milliseconds}</span>
        </div>
      </div>
      <div className="button-container">
        {(!isRunning && !isFinished) && <button type="button" onClick={handleStart}>Start</button>}
      </div>
    </div>
  );
};
