/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable max-len */
/* eslint-disable no-restricted-syntax */
/* eslint-disable no-param-reassign */
/* eslint-disable jsx-a11y/label-has-associated-control */
import React, { useMemo, useState } from 'react';
import classNames from 'classnames';
import ReactAudio from 'react-h5-audio-player';
import { Link } from 'react-router-dom';
import audio from '../../audio/audio_shapes_and_color.mp3';

import { configuration_7 } from '../../configurations/configuration_7';

export const Test7:React.FC = () => {
  const [tests, setTests] = useState(configuration_7);
  const [userId, setUserId] = useState<null | number>(null);
  const [comments, setComments] = useState('No comment');
  const [i, setI] = useState(0);

  const [color, setColor] = useState('green');
  const colorWords = useMemo(() => {
    if (color.localeCompare('green') === 0) {
      return 'зеленого кольору';
    }

    if (color.localeCompare('red') === 0) {
      return 'червоного кольору';
    }

    if (color.localeCompare('yellow') === 0) {
      return 'жовтого кольору';
    }

    if (color.localeCompare('blue') === 0) {
      return 'блакитного кольору';
    }

    return 'будь-якого кольору';
  }, [color]);

  const handleChoiceChange = (id: number, choice: string) => {
    const newArr = [...tests].map((test) => {
      if (test.id === id) {
        if (test.choice.localeCompare(choice) !== 0) {
          test.choice = choice;
        } else {
          test.choice = '';
        }
      }

      return test;
    });

    setColor(tests.find(test => test.id === id + 1)?.color || '');
    setTests(newArr);
  };

  const [isFinished, setIsFinished] = useState(false);
  const [isFinishedQuestion, setIsFinishedQuestion] = useState(false);
  const [score, setScore] = useState(0);
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [res, setRes] = useState('');

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    if (i < tests.length - 1) {
      return;
    }

    const sumWithInitial = tests.reduce(
      (accumulator, cur) => {
        if (cur.choice === cur.correct) {
          return accumulator + 1;
        }

        return accumulator;
      },
      0,
    );
    const rate = Math.round((sumWithInitial / tests.length) * 100);
    const comm = `Даний тест підвищеної складності, розрахований на 20с було пройдено на ${rate} % від можливих, при прослуховуванні двома вухами. Знайдено відповідник з трьох використаних назв геометричних фігур та чотирьох назв кольорів ${sumWithInitial} разів. `;
    setComments(comm);

    const scriptUrl = 'https://script.google.com/macros/s/AKfycbzBoQKaLpRBj9RoBfCaMDJGRvWYA8MWOOk5UgxlVeaex_q8K_0v3Po7U_V_bz7hXbxI/exec';
    const k = {
      userId,
      testId: 7,
      result: sumWithInitial,
      comments: comm
    };

    fetch(scriptUrl, { method: 'POST', body: JSON.stringify(k) })
      .then(() => {
        // eslint-disable-next-line no-console
        console.log('SUCCESSFULLY SUBMITTED');
      })
      // eslint-disable-next-line no-console
      .catch(err => console.log(err));

    setScore(sumWithInitial);
    setIsFinished(true);
  };

  const handleQuestionChange = () => {
    if (i < tests.length - 1) {
      setIsFinishedQuestion(true);
      setTimeout(() => {
        setI(prev => prev + 1);
        setIsFinishedQuestion(false);
      }, 500);
    }
  };

  return (
    <>
      <div className="progress-bar">
        <Link
          className="progress-bar__link"
          to="../"
        >
          &lt;&lt; Повернутися до тестів
        </Link>
        <h2 className="progress-bar__title">Тест 7</h2>
        <div className="progress-bar__progress">
          <div className="progress-bar__progress__level">
            <div className="progress-bar__progress__level__text">{`Прогрес ${i + 1}/${tests.length}`}</div>
            <div className="progress-bar__progress__level__container">
              <div className="progress-bar__progress__level__container__empty"></div>
              <div className={`progress-bar__progress__level__container__fill progress-bar__progress__level__container__fill--${
                Math.round(((i + 1) / tests.length) * 100)}`}
              >
              </div>
            </div>
          </div>
        </div>
      </div>

      <form className="form" onSubmit={handleSubmit}>
        <div className="form_section">
          <div className="form_section__question">
            <label htmlFor="userId"> Щоб зберегти результати, введіть будь-ласка ваш Id:</label>

            <input
              type="number"
              id="userId"
              name="userId"
              min="1"
              value={String(userId)}
              onChange={(e) => setUserId(Number(e.target.value))}
            />
          </div>
          <h3 className="form_title">
            Натисніть на елемент <span className={`form_title__color form_title__color--${color}`}>{colorWords}</span>, який почуєте, якщо він наявний на екрані.
          </h3>
          <div className="form_section__question">
            <div className="audio-player-container">
              <ReactAudio
                className="audioplayer"
                style={{ backgroundColor: '#C2E2C0', width: '300px' }}
                src={audio}
                layout="horizontal-reverse"
                showJumpControls={false}
              />
            </div>

            <div className={classNames(
              // 'form_section__question',
              'form_section__question__answers',
              { form_section__question__answers__wrong: tests[i].correct !== tests[i].choice && isFinishedQuestion },
              { form_section__question__answers__right: tests[i].correct === tests[i].choice && isFinishedQuestion },
            )}
            >
              <div>
                <input
                  type="radio"
                  id={`${tests[i].audio}_${tests[i].options[0]}`}
                  name={tests[i].audio}
                  value={tests[i].options[0]}
                  onChange={() => handleChoiceChange(tests[i].id, tests[i].options[0])}
                  onClick={handleQuestionChange}
                  checked={tests[i].choice === tests[i].options[0]}
                  required
                />
                <label htmlFor={`${tests[i].audio}_${tests[i].options[0]}`}>
                  <img
                    className="image-option"
                    src={tests[i].options[0]}
                  />
                </label>
              </div>

              <div>
                <input
                  type="radio"
                  id={`${tests[i].audio}_${tests[i].options[1]}`}
                  name={tests[i].audio}
                  value={tests[i].options[1]}
                  onChange={() => handleChoiceChange(tests[i].id, tests[i].options[1])}
                  onClick={handleQuestionChange}
                  checked={tests[i].choice === tests[i].options[1]}
                  required
                />
                <label htmlFor={`${tests[i].audio}_${tests[i].options[1]}`}>
                  <img
                    className="image-option"
                    src={tests[i].options[1]}
                  />
                </label>
              </div>

              <div>
                <input
                  type="radio"
                  id={`${tests[i].audio}_${tests[i].options[2]}`}
                  name={tests[i].audio}
                  value={tests[i].options[2]}
                  onChange={() => handleChoiceChange(tests[i].id, tests[i].options[2])}
                  onClick={handleQuestionChange}
                  checked={tests[i].choice === tests[i].options[2]}
                  required
                />
                <label htmlFor={`${tests[i].audio}_${tests[i].options[2]}`}>
                  <img
                    className="image-option"
                    src={tests[i].options[2]}
                  />
                </label>
              </div>

              <div>
                <input
                  type="radio"
                  id={`${tests[i].audio}_${tests[i].options[3]}`}
                  name={tests[i].audio}
                  value={tests[i].options[3]}
                  onChange={() => handleChoiceChange(tests[i].id, tests[i].options[3])}
                  onClick={handleQuestionChange}
                  checked={tests[i].choice === tests[i].options[3]}
                  required
                />
                <label htmlFor={`${tests[i].audio}_${tests[i].options[3]}`}>
                  <img
                    className="image-option"
                    src={tests[i].options[3]}
                  />
                </label>
              </div>
            </div>
          </div>
        </div>
        {!isFinished && (i === tests.length - 1) && (
          <button
            type="submit"
            className="button"
            onClick={handleQuestionChange}
          >
            Закінчити тест
          </button>
        )}

        {isFinished && (
          <div className="result">
            <h2>Results</h2>
            <h3>{`Your score: ${score}`}</h3>
            <h3>{`Your Result : ${comments}`}</h3>
          </div>
        )}

        <p>В разі трьох невдалих спроб пройти тест можна перейти до наступного. </p>

        <Link
          className="test__link"
          to="../test-8"
          title="Перейти до тесту 8"
        >
          До тесту 8
        </Link>
      </form>
    </>
  );
};
