/* eslint-disable no-console */
/* eslint-disable jsx-a11y/label-has-associated-control */
import React, { useState } from 'react';
import { CSVLink } from 'react-csv';
import './Overview.scss';

type Props = {};
type Res = [number, number, number, string];

export const Overview:React.FC<Props> = () => {
  const [userId, setUserId] = useState<null | number>(null);
  const [userScore, setUserScore] = useState<number[]>([]);
  const [userRes, setUserRes] = useState<string[]>([]);
  const [isFinished, setIsFinished] = useState(false);
  const [IsLoading, setIsLoading] = useState(false);
  const [datetime, setDatetime] = useState('');
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const [csvFile, setCsvFile] = useState<any>([]);
  const [finalComment, setFinalComment] = useState('')

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    const scriptUrl = 'https://script.google.com/macros/s/AKfycbzBoQKaLpRBj9RoBfCaMDJGRvWYA8MWOOk5UgxlVeaex_q8K_0v3Po7U_V_bz7hXbxI/exec';

    setTimeout(() => {
      setIsLoading(true);
      fetch(scriptUrl, { method: 'GET' })
        .then((res) => {
          return res.json();
        })
        .then((res) => {
          const arr = res.filter((el :number[] | string []) => el[0] === userId);
          const arrScore:number[] = [];
          const arrComm:string[] = [];

          console.log(JSON.stringify(arr));

          arr.forEach((el: Res) => {
            const id = el[1];
            const sc = el[2];
            const com = el[3];

            arrScore[id] = sc;
            arrComm[id] = com;
          });

          if (arrScore[10] > arrScore[11]) {
            setFinalComment(`Ви наразі ще маєте непоганий слух, але вже не ідеальний, тому не забувайте перевіряти його час від часу та користуватись бервухами для його подальшого збереження. Чутність лівим вухом краща, а правим вухом гірша`);
          } else if (arrScore[10] < arrScore[11]) {
            setFinalComment(`Ви наразі ще маєте непоганий слух, але вже не ідеальний, тому не забувайте перевіряти його час від часу та користуватись бервухами для його подальшого збереження. Чутність правим вухом краща, а лівим вухом гірша.`);
          } else {
            setFinalComment(`Ви наразі ще маєте непоганий слух, але вже не ідеальний, тому не забувайте перевіряти його час від часу та користуватись бервухами для його подальшого збереження. Чутність правим та лівим вухом на одному рівні.`);
          }

          setUserScore([...arrScore]);
          setUserRes([...arrComm]);
          setIsFinished(true);
          setDatetime(new Date().toLocaleString());
          const csvData = [
            [...res[0]],
            ...arr,
          ];

          setCsvFile(csvData);
          setIsLoading(false);
        })
      // eslint-disable-next-line no-console
        .catch(err => console.log(err));
    }, 100);
  };

  return (
    <>
      <form className="form" onSubmit={handleSubmit}>
        <div className="form_section">
          <h3 className="form_title">Результати</h3>
          <div className="form_section__question">
            <label htmlFor="userId"> Щоб отримати результати, введіть будь-ласка ваш Id:</label>

            <input
              type="number"
              id="userId"
              name="userId"
              min="0"
              value={String(userId)}
              onChange={(e) => setUserId(Number(e.target.value))}
            />

            <button
              className="test__link"
              type="submit"
            >
              Отримати результат
            </button>
          </div>
        </div>
        {IsLoading && <div className="loader"></div>}
        <div className="form_section">
          {isFinished && (
            <>
              <div className="overview">
                <table className="overview_table">
                  <tbody>
                    {userScore.map((el, index) => (
                    // eslint-disable-next-line react/no-array-index-key
                      <tr className="overview_item" key={index}>
                        {index === 0
                          ? <th className="overview_item__option overview_item__option--title">{`Результати для користувача з id: ${userId}`}</th>
                          : (
                            <td>
                              <div className="overview_item__option overview_item__option--strong">{`Test №${index}`}</div>
                              <div className="overview_item__option">{`Результат ${el} балів`}</div>
                              <div className="overview_item__option">{`${userRes[index]}`}</div>
                            </td>
                          )}
                      </tr>
                    ))}
                    <tr className="overview_item">
                      <td>
                        <div className="overview_item__option">
                          {finalComment}
                        </div>
                      </td>
                    </tr>
                  </tbody>
                  <tfoot>
                    <div>ProjectHear</div>
                    <div className="">{datetime}</div>
                  </tfoot>
                </table>
              </div>
              <CSVLink className="download downloadbtn" filename="Result_ProjectHear.csv" data={csvFile}>
                Завантажити результати в CSV форматі
              </CSVLink>
            </>
          )}
        </div>
      </form>
    </>
  );
};
