/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable max-len */
/* eslint-disable no-restricted-syntax */
/* eslint-disable no-param-reassign */
/* eslint-disable jsx-a11y/label-has-associated-control */
import React, { useState } from 'react';
// import { writeJsonFile } from 'write-json-file';
import classNames from 'classnames';
import ReactAudio from 'react-h5-audio-player';
import 'react-h5-audio-player/lib/styles.css';
import { Link } from 'react-router-dom';
import { Task } from '../../types/Task';

import './Test0.scss';
import { StopWatch } from '../StopWatch';

type Props = {
  id: number;
  name: string;
  task: string;
  configuration: Task[];
  isImage: boolean;
  isStopWatch: boolean;
  isVolume: boolean;
  // oneAudio: boolean;
};

export const Test0:React.FC<Props> = ({
  id,
  name,
  task,
  configuration,
  isImage,
  isStopWatch,
  isVolume,
  // oneAudio,
}) => {
  const [tests, setTests] = useState(configuration);
  const [i, setI] = useState(0);
  const [isFinished, setIsFinished] = useState(false);
  const [isStarted, setIsStarted] = useState(false);
  const [isFinishedQuestion, setIsFinishedQuestion] = useState(false);
  const [score, setScore] = useState(0);
  const [timeRes, setTimeRes] = useState('');
  const [userId, setUserId] = useState<null | number>(null);
  const [comments, setComments] = useState('No comment');
  // const [isEnter, setEnter] = useState(false);

  const handleChoiceChange = (num: number, choice: string) => {
    const newArr = [...tests].map((test) => {
      if (test.id === num) {
        if (test.choice.localeCompare(choice) !== 0) {
          test.choice = choice;
        } else {
          test.choice = '';
        }
      }

      return test;
    });

    setTests(newArr);
  };

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    if (i < tests.length - 1) {
      return;
    }

    const sumWithInitial = tests.reduce(
      (accumulator, cur) => {
        // eslint-disable-next-line no-console
        // console.log('cur.choice', cur.choice);
        // eslint-disable-next-line no-console
        // console.log('cur.correct', cur.correct);
        if (cur.choice === cur.correct) {
          return accumulator + 1;
        }

        return accumulator;
      },
      0,
    );
    // const tempL = [10];
    // const tempR = [10];
    const tempLR = [65];
    let comm = ''

    if (id === 1) {
      for (const el of tests) {
        if (el.choice === el.correct) {
          tempLR.push(65 + el.range);
        }
      }
      const rate = Math.min(...tempLR);
      comm = `Збережено чутність обома вухами разом на рівні ${rate} дБ`;
      setComments(comm);
    } else if (id === 2) {
      for (const el of tests) {
        if (el.choice === el.correct) {
          tempLR.push(65 + el.range);
        }
      }
      const rate = Math.min(...tempLR);
      comm = `Ви відрізнили різні за змістом, але схожі за звучанням слова до рівня ${rate} дБ при прослуховуванні двома вухами `;
      setComments(comm);
    } else if (id === 3) {
      for (const el of tests) {
        if (el.choice === el.correct) {
          tempLR.push(65 + el.range);
        }
      }
      const rate = Math.min(...tempLR);
      comm = `Ви відрізнили різні за змістом, але схожі за звучанням слова навіть в шумі до рівня ${rate} дБ при прослуховуванні двома вухами за умови збереження шуму на одному рівні і зменшення гучності промовлених слів.`;
      setComments(comm);
    } else if (id === 4) {
      const rate = Math.round((sumWithInitial / tests.length) * 100);
      comm = `Ви відрізнили різні за змістом, але схожі за звучанням слова та знаходите відповідник до них у ${rate} % випадків при прослуховуванні двома вухами`;
      setComments(comm);
    } else if (id === 5) {
      const t = Number(timeRes.split('хвилин')[0].trim()) + 1;
      const rate = Math.round((sumWithInitial / tests.length) * 100);
      comm = `Ви відрізнили різні за змістом, але схожі за звучанням слова та знаходите відповідник до них у ${rate} % випадків при прослуховуванні двома вухами менше ніж за ${t} хв.`;
      setComments(comm);
    } else if (id === 6 ) {
      const rate = Math.round((sumWithInitial / tests.length) * 100);
      comm = `Даний тест підвищеної складності, розрахований на 34 с було пройдено. Знайдено відповідник до промовлених різних за змістом, але схожих за звучанням слів серед представлених карток у ${rate} % випадків при прослуховуванні двома вухами`;
      setComments(comm);
    } else if (id === 7 ) {
      const rate = Math.round((sumWithInitial / tests.length) * 100);
      comm = `Даний тест підвищеної складності, розрахований на 20с було пройдено на ${rate} % від можливих, при прослуховуванні двома вухами. Знайдено відповідник з трьох використаних назв геометричних фігур та чотирьох назв кольорів ${sumWithInitial} разів. `;
      setComments(comm);
    } else if (id === 8 ) {
      const rate = Math.round((sumWithInitial / tests.length) * 100);
      comm = `Даний тест розрахований на 13с. Знайдено відповідник до промовлених 5 тризначних чисел за обмежений час у ${rate} % випадків при прослуховуванні двома вухами`;
      setComments(comm);
    } else if (id === 9 ) {
      const rate = Math.round((sumWithInitial / tests.length) * 100);
      comm = `Даний тест розрахований на 15с. Знайдено відповідник до промовлених 9 слів, які означають напрямок  з чотирьох значків у вигляді стрілок за обмежений час у ${rate} % випадків при прослуховуванні двома вухами `;
      setComments(comm);
    } else if (id === 10 ) {
      const tempL = [65];
      for (const el of tests) {
        if (el.choice === el.correct) {
          tempL.push(65 + el.range);
        }
      }
      const rate = Math.min(...tempL);
      comm = `Збережено чутність лівим вухом на рівні ${rate} дБ`;
      setComments(comm);
    } else if (id === 11 ) {
      const tempL = [65];
      for (const el of tests) {
        if (el.choice === el.correct) {
          tempL.push(65 + el.range);
        }
      }
      const rate = Math.min(...tempL);
      comm = `Збережено чутність правим вухом на рівні ${rate} дБ`;
      setComments(comm);
    } else if (id === 12 ) {
      const rate = Math.round((sumWithInitial / tests.length) * 100);
      comm = `Даний тест розрахований на 10с. Знайдено відповідник до 5 промовлених фраз за обмежений час у ${rate} % випадків при прослуховуванні двома вухами`;
      setComments(comm);
    }



    setScore(sumWithInitial);
    setIsFinished(true);

    const scriptUrl = 'https://script.google.com/macros/s/AKfycbzBoQKaLpRBj9RoBfCaMDJGRvWYA8MWOOk5UgxlVeaex_q8K_0v3Po7U_V_bz7hXbxI/exec';

    setIsFinishedQuestion(true);
    setTimeout(() => {
      setIsFinishedQuestion(false);

      const k = {
        userId,
        testId: id,
        result: sumWithInitial,
        comments: comm
      };

      // eslint-disable-next-line no-console
      console.log(k);

      fetch(scriptUrl, { method: 'POST', body: JSON.stringify(k) })
        .then((result) => {
          // mode: 'no-cors' body: JSON.stringify(d)
          // eslint-disable-next-line no-console
          console.log('SUCCESSFULLY SUBMITTED');
          // eslint-disable-next-line no-console
          console.log(result);
        })
        // eslint-disable-next-line no-console
        .catch(err => console.log(err));
    }, 100);
  };

  const handleQuestionChange = () => {
    if (i < tests.length - 1) {
      setIsFinishedQuestion(true);
      setTimeout(() => {
        setI(prev => prev + 1);
        setIsFinishedQuestion(false);
      }, 100);
    }
  };

  return (
    <>
      <div className="progress-bar">
        <Link
          className="progress-bar__link"
          to="../"
        >
          &lt;&lt; Повернутися до тестів
        </Link>
        <h2 className="progress-bar__title">{`Тест ${id}: ${name}`}</h2>
        <div className="progress-bar__progress">
          <div className="progress-bar__progress__level">
            <div className="progress-bar__progress__level__text">{`Питання ${i + 1}/${tests.length}`}</div>
            <div className="progress-bar__progress__level__container">
              <div className="progress-bar__progress__level__container__empty"></div>
              <div className={`progress-bar__progress__level__container__fill progress-bar__progress__level__container__fill--${
                Math.round(((i + 1) / tests.length) * 100)}`}
              >
              </div>
            </div>
          </div>
          {isVolume && (
            <div className="progress-bar__progress__volume">
              <div className="progress-bar__progress__volume__text">Поточна гучність</div>
              <div className="progress-bar__progress__volume__container">
                <div className="progress-bar__progress__volume__container__empty "></div>
                <div className={`progress-bar__progress__volume__container__fill progress-bar__progress__volume__container__fill--${
                  Math.round(((tests.length - i) / tests.length) * 100)}`}
                >
                </div>
              </div>
            </div>
          )}
        </div>
      </div>

      <form className="form" onSubmit={handleSubmit}>
        <div className="form_section">
          <h3 className="form_title">{task}</h3>
          <div className="form_section__question">
            <label htmlFor="userId"> Щоб зберегти результати, введіть будь-ласка ваш Id:</label>

            <input
              type="number"
              id="userId"
              name="userId"
              min="0"
              value={String(userId)}
              onChange={(e) => setUserId(Number(e.target.value))}
            />
          </div>
          {isStopWatch && (
            <StopWatch
              setTimeRes={setTimeRes}
              isFinished={isFinished}
              setIsStarted={setIsStarted}
            />
          )}
          {((isStarted && isStopWatch) || !isStopWatch) && (
            <div
              className={classNames(
                'form_section__question',
                { form_section__question__wrong: tests[i].correct !== tests[i].choice && isFinishedQuestion },
                { form_section__question__right: tests[i].correct === tests[i].choice && isFinishedQuestion },
              )}
              key={tests[i].audio}
            >
              <div className="audio-player-container">
                <ReactAudio
                  className="audioplayer"
                  style={{ backgroundColor: '#C2E2C0', width: '300px' }}
                  src={tests[i].audio}
                  layout="horizontal-reverse"
                  showJumpControls={false}
                />
              </div>

              <div className="form_section__question__answers">
                {tests[i].options.map((el, index) => (
                  <div key={`${tests[i].id} ${el}`}>
                    <input
                      type="radio"
                      id={`${i}_${index}`}
                      name={tests[i].audio}
                      value={tests[i].options[index]}
                      onChange={() => handleChoiceChange(i, tests[i].options[index])}
                      checked={tests[i].choice === tests[i].options[index]}
                      required
                    />
                    <label htmlFor={`${i}_${index}`}>
                      {isImage
                        ? (
                          <img
                            className="image-option"
                            src={tests[i].options[index]}
                            alt="optionImage"
                          />
                        )
                        : tests[i].options[index]}
                    </label>
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>
        {((isStarted && isStopWatch) || !isStopWatch) && !isFinished && (
          <button
            type={i < tests.length - 1
              ? 'button'
              : 'submit'}
            className="button"
            onClick={handleQuestionChange}
          >
            {i < tests.length - 1
              ? 'Далі'
              : 'Закінчити тест'}
          </button>
        )}

        {((isStarted && isStopWatch) || !isStopWatch) && isFinished && (
          <div className="result">
            <h2>Results</h2>
            <h3>{`Your score: ${score}`}</h3>
            {isStopWatch && (
              <p>{`Your time: ${timeRes}`}</p>
            )}
            <h3>{`Your Result : ${comments}`}</h3>
          </div>
        )}

        {((isStarted && isStopWatch) || !isStopWatch) && isFinished && (id < 12) && (
          <Link
            className="test__link"
            to={`../test-${id + 1}`} // "../test-2"
            title={`Перейти до тесту ${id + 1}`} // "Перейти до тесту 2"
          >
            {`До тесту ${id + 1}`}
          </Link>
        )}

        {((isStarted && isStopWatch) || !isStopWatch) && isFinished && (id === 12) && (
          <>
            <p>Ви пройшли всі 12 етапів перевірки слуху і швидкості реакції і по кожному окремо отримали результат. При бажанні з часом можна пройти все тестування або будь-які його етапи окремо і порівняти результати. Наступного разу можна буде використовувати той самий id, або зареєструватись знову.</p>
            <p>Дякуємо за участь у тестуванні</p>
            <p>💖</p>
            <Link
              className="test__link"
              to={`/overview`} // "../test-2"
              title={`Перейти до результатів`}
            >
              {`До результатів`}
            </Link>
          </>
        )}

        {((isStarted && isStopWatch) || !isStopWatch) && !isFinished && (id === 6) && (
          <>
            <p>В разі трьох невдалих спроб пройти тест можна перейти до наступного. </p>

            <Link
              className="test__link"
              to={`../test-${id + 1}`} // "../test-2"
              title={`Перейти до тесту ${id + 1}`}
            >
              {`До тесту ${id + 1}`}
            </Link>
          </>
        )}
      </form>

    </>
  );
};
