/* eslint-disable max-len */
/* eslint-disable no-restricted-syntax */
/* eslint-disable no-param-reassign */
/* eslint-disable jsx-a11y/label-has-associated-control */
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import ReactAudio from 'react-h5-audio-player';
import 'react-h5-audio-player/lib/styles.css';
import audio1 from '../../audio/Test_01/L_0.mp3';
import audio2 from '../../audio/Test_01/R_0.mp3';

import './Settings.scss';

export const Settings:React.FC = () => {
  const [age, setAge] = useState(0);
  const [sex, setSex] = useState('o');
  const [isMilitary, setIsMilitary] = useState(false);
  const [id, setId] = useState(0);
  const [isSaved, setIsSaved] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const scriptUrl = 'https://script.google.com/macros/s/AKfycbx3TTYnwrJoZdojrTzs6puU_5LfGwaRYkUquYeBhKr_6oRjEN-Iz63QTC2u-2H-ceyH/exec';

  useEffect(() => {
    fetch(scriptUrl, { method: 'GET' })
      .then((res) => {
        // mode: 'no-cors' body: JSON.stringify(d)
        // eslint-disable-next-line no-console
        console.log('SUCCESSFUL GET REQUEST');
        // eslint-disable-next-line no-console
        console.log(res);

        return res.json();
      })
      .then((res) => {
        // eslint-disable-next-line no-console
        console.log(res);
        // eslint-disable-next-line no-console
        console.log(res[res.length - 1][0]);
        setId(res[res.length - 1][0] + 1);
        const d = {
          id: res[res.length - 1][0] + 1,
          age,
          sex,
          isMilitary,
        };

        setIsLoading(false);

        if (age > 0) {
          fetch(scriptUrl, { method: 'POST', body: JSON.stringify(d) })
            .then((result) => {
              // mode: 'no-cors' body: JSON.stringify(d)
              // eslint-disable-next-line no-console
              console.log('SUCCESSFULLY SUBMITTED');
              // eslint-disable-next-line no-console
              console.log(result);
            })
            // eslint-disable-next-line no-console
            .catch(err => console.log(err));
        } else {
          setIsSaved(false);
        }
      })
      // eslint-disable-next-line no-console
      .catch(err => console.log(err));
  }, [isSaved === true]);

  return (
    <>
      <div className="progress-bar progress-bar--settings">
        <Link
          className="progress-bar__link"
          to="../"
        >
          &lt;&lt; Повернутися до тестів
        </Link>
        <h2 id="general" className="progress-bar__title">Налаштування та підготовка до проходження тестів</h2>
      </div>

      <form className="form">
        <div className="form_section">
          <h3>
            <strong>Загальна інформація</strong>
            <br />
            Виставити гучність на 20 % від можливого (якщо гучність додається з 0 до максимального значення за 15 кроків, то встановлюємо на 3).
            <br />
            У випадку, якщо випадково зробили не ту позначку або зробили позначку до включення запису - оновіть сторінку. Якщо немає змоги оновити сторінку можна зайти в додаток повторно і позначивши свій id, пройти тестування з того тесту на якому зупинились
          </h3>
          <h3>
            Зараз ви почуєте звук з лівого навушника. Переконайтесь, що цей навушник знаходиться на лівому вусі
          </h3>
          <div className="audio-player-container">
            <ReactAudio
              className="audioplayer"
              style={{ backgroundColor: '#C2E2C0', width: '300px' }}
              src={audio1}
              layout="horizontal-reverse"
              showJumpControls={false}
            />
          </div>

          <h3>
            Зараз ви почуєте звук з правого навушника. Переконайтесь, що цей навушник знаходиться на правому вусі
          </h3>
          <div className="audio-player-container">
            <ReactAudio
              className="audioplayer"
              style={{ backgroundColor: '#C2E2C0', width: '300px' }}
              src={audio2}
              layout="horizontal-reverse"
              showJumpControls={false}
            />
          </div>
        </div>
        <div className="form_section">
          <h3>
            Щоб зберегти результати, пройдіть будь-ласка коротке опитування
          </h3>

          <div className="form_section__question">
            <label htmlFor="age">Ввведіть ваш вік:</label>

            <input
              type="number"
              id="age"
              name="age"
              min="3"
              max="100"
              value={String(age)}
              onChange={(e) => setAge(Number(e.target.value))}
            />
          </div>

          <div className="form_section__question">
            <h3>Виберіть вашу стать:</h3>

            <div className="border">
              <label htmlFor="f">Жінка:</label>

              <input
                type="radio"
                id="f"
                name="sex"
                value={sex}
                onChange={() => setSex('f')}
                checked={sex === 'f'}
                required
              />
            </div>

            <div className="border">
              <label htmlFor="m">Чоловік:</label>

              <input
                type="radio"
                id="m"
                name="sex"
                value={sex}
                onChange={() => setSex('m')}
                checked={sex === 'm'}
                required
              />
            </div>

            <div className="border">
              <label htmlFor="o">Не вказувати :</label>

              <input
                type="radio"
                id="o"
                name="sex"
                value={sex}
                onChange={() => setSex('o')}
                checked={sex === 'o'}
                required
              />
            </div>

          </div>

          <div className="form_section__question">
            <h3>Перебували на фронті чи в зоні обстрілів:</h3>

            <div className="border">
              <label htmlFor="">Так:</label>

              <input
                type="radio"
                id="yes"
                name="isMilitary"
                value={
                  isMilitary
                    ? 'yes'
                    : 'no'
                }
                onChange={() => setIsMilitary(true)}
                checked={isMilitary === true}
                required
              />
            </div>

            <div className="border">
              <label htmlFor="no">Ні:</label>

              <input
                type="radio"
                id="no"
                name="isMilitary"
                value={
                  isMilitary
                    ? 'yes'
                    : 'no'
                }
                onChange={() => setIsMilitary(false)}
                checked={isMilitary === false}
                required
              />
            </div>
          </div>

        </div>
        <button
          className="test__link"
          type="button"
          onClick={() => {
            setIsSaved(true);
            setIsLoading(true);
          }}
        >
          Отримати id
        </button>
      </form>
      {(age > 0) && (isSaved === true) && (isLoading === false) && (
        <div className="userIdSection">
          <h3>Ваш Id:</h3>
          <h3>{id}</h3>
        </div>
      )}
      {(age > 0) && (isSaved === true) && (isLoading === true) && (
        <div className="userIdLoading">
          <h3>Будь-ласка зачекайте</h3>
          <h3>Loading...</h3>
        </div>
      )}
      {(age === 0) && (isSaved === true) && (isLoading === true) && (
        <div className="userIdLoading">
          <h3>Вік введено неправильно</h3>
          <h3>Будь-ласка введіть свій вік</h3>
        </div>
      )}
      <div className="test__link_div">
        <Link
          className="test__link"
          to="../test-1"
          title="Перейти до тесту 1"
        >
          До тесту 1
        </Link>
      </div>
    </>
  );
};
